.section-first-screen {
	background: no-repeat left;
	background-size: cover;
	padding-top: 46px;
	padding-bottom: 55px;

  @include media-q(min, 768) {
		background: no-repeat center;
		padding-top: 166px;
		padding-bottom: 185px;
	}
}

.first-screen-title {
	font-size: 34px;
	color: #fff;
	text-transform: uppercase;
	line-height: 1.2;
	margin-bottom: 7px;
	font-weight: 700;
	position: relative;

	@include media-q(min, 768) {
		font-size: 46px;
	}
}

.first-screen-text {
	font-size: 22px;
	font-weight: 300;
	color: #fff;
	margin-bottom: 52px;
	letter-spacing: 0.6px;

	@include media-q(min, 768) {
		margin-bottom: 0;
		font-size: 42px;
	}
}

